.classroom-card {
  @include vpfo-box-shadow;
  border: 1px solid transparent;

  &:not(:first-child) {
    margin-top: 1rem;
  }

  .accent {
    background-color: $learning-spaces-blue-light;
    width: 100%;
    height: 1rem;
    top: 0;
    left: 0;

    @include media-breakpoint-up(md) {
      height: 100%;
      width: 1rem;
    }
  }

  .classroom-thumbnail {
    width: 100%;
    aspect-ratio: 2/1;

    @include media-breakpoint-up(md) {
      width: 13.75rem;
      min-width: 13.75rem;
      aspect-ratio: 3/2;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &.no-image {
      aspect-ratio: unset;

      @include media-breakpoint-up(md) {
        width: 13.75rem;
        min-width: 13.75rem;
        aspect-ratio: 3/2;
        background: $learning-spaces-blue-light;
      }
    }
  }

  h2 {
    line-height: 1.125;
  }

  .classroom-building-name {
    color: $ubc-brand-blue;
    line-height: 1;
  }

  dl {
    margin: 0;

    dt {
      font-weight: 500;
      text-transform: uppercase;
      margin: 0;
      line-height: 1.875rem;

      @include media-breakpoint-up(md) {
        font-size: 0.75rem;
        line-height: 1.25rem;
      }
    }

    dd {
      font-size: 1rem;
      margin: 0;

      @include media-breakpoint-up(md) {
        font-size: 0.875rem;
      }
    }
  }

  &:hover, &:active, &:focus, &:focus-within {
    border-color: $ubc-brand-blue;

    .accent {
      background-color: $ubc-brand-blue;
    }
  }
}