// import only the bootstrap assets we need here to use their breakpoint mixins with our custom styles
@import "~bootstrap/scss/functions";
@import "~bootstrap/scss/variables";
@import "~bootstrap/scss/mixins";

.vpfo-spaces-page {
	-webkit-font-smoothing: antialiased;
	font-family: $font-whitney;
	color: $ubc-blue;
	overflow-x: hidden;

	h1 {
		font-size: 2.25rem;
		line-height: 1.1667;
		font-weight: 700;
	}

	p {
		margin: 1rem 0;

		&:first-child {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
		}
	}

  img {
    max-width: 100%;
    height: auto;
  }

	.btn {
		@include btn;
	}

	.btn-primary {
		@include btn-primary;
	}

	.btn-secondary {
		@include btn-secondary;
	}

	.btn-border-thick {
		border-width: 0.125rem;
	}

	.breadcrumb {
		font-size: 0.875rem;
		font-weight: 500;
		line-height: 1.375rem;
		height: auto;
		min-height: unset;
		gap: 0.75rem 0;

		.current-page {
			color: $ubc-brand-blue;
		}

		a {
			color: $ubc-blue;
			text-decoration: none;

			&:hover, &:focus, &:active {
				outline: none;
				text-decoration: underline;
			}
		}

		i {
			font-size: 0.5rem;
		}
	}

	.alert-message {
		background: $ubc-brand-blue;
		color: $utility-white;
		font-size: 1.125rem;
		line-height: 1.5;
		font-weight: 500;
		gap: 1rem 0.75rem;

		i {
			font-size: 1rem;
		}

		@include media-breakpoint-up(sm) {
			font-size: 1rem;
		}
	}

